<template>
  <v-container>
    <v-row justify="center" id="contact">
      <v-col cols="12" md="8">
        <v-card color="primary darken-3">
          <v-form class="px-3 px-md-6">
            <v-row justify="space-around">
              <v-col cols="12" md="6">
                <v-text-field
                  label="First Name"
                  color="white"
                  v-model="form.firstName"
                  :rules="[rules.required, rules.counter]"
                  counter
                  maxlength="20"
                />
              </v-col>

              <v-col cols="12" md="6">
                <v-text-field
                  label="Last Name"
                  color="white"
                  v-model="form.lastName"
                  :rules="[rules.required, rules.counter]"
                  counter
                  maxlength="20"
                />
              </v-col>
            </v-row>

            <v-row align="center" justify="center">
              <v-col cols="12">
                <v-select
                  label="Reason for your enquiry"
                  :items="reasons"
                  color="white"
                  v-model="form.reasonOfContact"
                  :rules="[rules.required]"
                />
              </v-col>
            </v-row>

            <v-row>
              <v-col cols="12" xs="12" md="6">
                <v-text-field
                  label="Email"
                  color="white"
                  v-model="form.email"
                  :rules="[rules.required, rules.email]"
                />
              </v-col>

              <v-col cols="12" xs="12" md="6">
                <v-text-field
                  label="Current Website (Optional)"
                  color="white"
                  v-model="form.currentWebsite"
                />
              </v-col>
            </v-row>

            <v-row>
              <v-col cols="12">
                <v-textarea
                  label="Message"
                  color="white"
                  v-model="form.message"
                  :rules="[rules.required, rules.counter1500]"
                  counter
                  maxlength="1500"
                />
              </v-col>
            </v-row>

            <v-row justify="center" align="center">
              <v-col xs="12" md="6">
                <v-btn width="100%" color="success" @click.prevent="submitForm">
                  Submit
                </v-btn>
              </v-col>
            </v-row>
          </v-form>
        </v-card>
      </v-col>
    </v-row>
    <v-row>
      <v-col cols="4" offset-md="4">
        <h3 align="center">
          <a href="mailto://contact@arb-web-design.me">
            contact@arb-web-design.me
          </a>
        </h3>
      </v-col>
    </v-row>
  </v-container>
</template>

<script>
const app = require("./../js/firebaseInit");
export default {
  name: "Contact",
  data() {
    return {
      reasons: [
        "Buying a Website",
        "Website Design",
        "Purchasing a Domain",
        "Purchase Website Hosting",
        "Bespoke Package",
        "Website Redesign",
        "Business Email Addresses"
      ],
      form: {
        firstName: null,
        lastName: null,
        reasonOfContact: null,
        email: null,
        currentWebsite: null,
        message: null
      },
      rules: {
        required: value => !!value || "Required",
        counter: value => value.length <= 20 || "Max 20 characters",
        counter1500: value => value.length <= 1500 || "Max 1500 characters",
        email: value => {
          const pattern = /^(([^<>()[\]\\.,;:\s@"]+(\.[^<>()[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/;
          return pattern.test(value) || "Invalid Email";
        }
      }
    };
  },
  methods: {
    submitForm() {
      app.makeEnquiry(this.form);

      this.form = {
        firstName: null,
        lastName: null,
        reasonOfContact: null,
        email: null,
        currentWebsite: null,
        message: null
      };
    }
  }
};
</script>
